<template>
  <div class="search">
    <van-nav-bar fixed title="商品列表" left-arrow @click-left="$router.go(-1)" />

    <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value="queryGoodsName||'搜索商品'"
      show-action
      @click="$router.push('/search')"
    >
      <template #action>
        <van-icon
        class="tool"
        name="apps-o"
        @click="$router.push('/category')"
        />
      </template>
    </van-search>

    <!-- 排序选项按钮 -->
    <div class="sort-btns">
      <div class="sort-item" @click="sort1">综合</div>
      <div class="sort-item" @click="sort2">销量</div>
      <div class="sort-item" @click="sort3">价格 </div>
    </div>

    <div class="goods-list">
      <GoodsItem v-for="item in searchGoodsList" :key="item.goods_id" :item="item"></GoodsItem>
    </div>
  </div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem.vue'
import { searchListApi } from '@/api/search'
export default {
  name: 'SearchIndex',
  data () {
    return {
      searchGoodsList: [],
      sortType: 'all',
      sortPrice: 0,
      page: 1
    }
  },
  created () {
    this.searchList()
  },
  methods: {
    async searchList () {
      const res = await searchListApi({
        sortType: this.sortType,
        sortPrice: this.sortPrice,
        goodsName: this.queryGoodsName,
        categoryId: this.queryCategoryId,
        page: this.page
      })
      // console.log(res.data.list.data)
      this.searchGoodsList = res.data.list.data
    },
    sort1 () {
      this.sortType = 'all'
      this.sortPrice = 0
      this.searchList()
    },
    sort2 () {
      this.sortType = 'sales'
      this.sortPrice = 0
      this.searchList()
    },
    sort3 () {
      this.sortType = 'price'
      if (this.sortPrice === 0) this.sortPrice = 1
      else if (this.sortPrice === 1) this.sortPrice = 0
      this.searchList()
    }

  },
  computed: {
    queryGoodsName () {
      return this.$route.query.search
    },
    queryCategoryId () {
      return this.$route.query.categoryId
    }
  },
  components: {
    GoodsItem
  }
}
</script>

<style lang="less" scoped>
.search {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .tool {
    font-size: 24px;
    height: 40px;
    line-height: 40px;
  }

  .sort-btns {
    display: flex;
    height: 36px;
    line-height: 36px;
    .sort-item {
      text-align: center;
      flex: 1;
      font-size: 16px;
    }
  }
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}
</style>
